import { Component, OnInit, Inject, Input, Output, EventEmitter, ViewChild, ChangeDetectorRef } from '@angular/core';

@Component({
  selector: 'app-stock-journal-view',
  templateUrl: './stock-journal-view.component.html',
  styleUrls: ['./stock-journal-view.component.scss']
})
export class StockJournalViewComponent implements OnInit {

  @Output() cancelData = new EventEmitter();
  @Input('ViewData') ViewData;

  sjData:any;

  constructor() { }

  ngOnInit() {
    this.sjData = this.ViewData;
    console.log(this.sjData);
  }

  cancelBtn() {
    this.cancelData.emit(true);
  }
  
}



