import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { StockJournalComponent } from './stock-journal.component';
import { SharedModule } from '../../../shared/modules/shared.module';
import { StockJournalRoutingModule } from './stock-journal-routing.module';
import { StockJournalAddComponent } from './stock-journal-add/stock-journal-add.component';
import { StockJournalViewComponent } from './stock-journal-view/stock-journal-view.component';
import { StockJournalEditComponent } from './stock-journal-edit/stock-journal-edit.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    StockJournalRoutingModule
  ],
  declarations: [
    StockJournalComponent,
    StockJournalAddComponent,
    StockJournalViewComponent,
    StockJournalEditComponent
  ],
  entryComponents:[
    StockJournalAddComponent,
    StockJournalViewComponent,
    StockJournalEditComponent
  ]
})
export class StockJournalModule { }



